.content {
    max-width: 1535px;
    margin: 0 auto;
}
.head {
    background-image: url(../img/head_bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-color: #202448;
    &__h1 {
        padding-top: 54px;
        display: flex;
        justify-content: space-between;
        &::before {
            content: "";
            display: inline-block;
            width: 189px;
            height: 33px;
            background-image: url(../img/head_logo.png);
            background-repeat: no-repeat;
        }
        &__text {
            color: #ffffff;
            font-family: 'Proxima_Nova_Lt';
            font-size: 14px;            
            line-height: 34px;
            text-transform: uppercase;
            letter-spacing: 0.7px;
        }
    }
    &__midle {
        padding-top: 309px;   
        &__t {            
            color: #ffffff;
            font-family: 'Proxima_Nova_Bold';
            font-size: 90px;            
            line-height: 100px;
            text-transform: uppercase;            
            letter-spacing: 4.5px;
        } 
        &__right {
            position: relative;
            display: flex;            
            align-items: flex-end;
            flex-direction: column;
            &::before {
                content: "";
                display: block;
                position: absolute;
                width: 254px;
                height: 317px;
                background-image: url(../img/poloska.png);
                align-self: flex-start;
                left: 400px;
                bottom: 0;
            }
        }
        &__nav {
            display: flex; 
        }    
        &__right_nav {
            margin-right: 54px;
            color: #9071ea;
            font-family: 'Proxima_Nova_Lt';
            font-size: 14px;            
            line-height: 34px;
            text-transform: uppercase;            
            letter-spacing: 0.7px;
            &:last-child {
                margin-right: 0;
            }
        }
    }
    &__down {
        padding-top: 181px;
        padding-bottom: 110px;
        display: flex;
        justify-content: center;
        &__button {
            text-decoration: none;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 20.26%;
            margin-right: 66px;
            box-shadow: 0 16px 24px #0a0e34;
            background-color: #5e42b0;
            border-radius: 50px;
            &::before {
                content: "";
                background-image: url(../img/icon_apple.png);
                width: 33px;
                height: 37px;
                margin-right: 29px
            }
            &__text {
                margin: 22px 0;
                color: #ffffff;                
                font-size: 14px;                
                // line-height: 34px;
                text-transform: uppercase;
                letter-spacing: 0.7px;
                &_up {                    
                    font-family: 'Proxima_Nova_Lt';
                }
                &_do {
                    font-family: 'Proxima_Nova_Semi';
                }
            }
            &:last-child {
                margin-right: 0px;
                &::before {
                    content: "";
                    background-image: url(../img/icon_google.png);
                    width: 36px;
                    height: 39px;
                }
            }
        }
    }
}
.part2 {
    &__wrap {
        display: flex;
        justify-content: flex-end;
        &__block {
            // margin-top: 112px;
            width: 50%;
        }
    }
    &__h1 {
        margin-top: 112px;
        color: #e7e7e7;
        font-family: 'Proxima_Nova_Bold';
        font-size: 90px;
        line-height: 60px;
        text-transform: uppercase;
    }
    &__cart {
        padding-top: 90px;
        padding-bottom: 60px;
        &__head {
            display: flex;
            align-items: center;
            font-family: 'Proxima_Nova_Bold';
            line-height: 34px;
            text-transform: uppercase;
            &__nomber {
                color: #000000;
                font-size: 18px;
                letter-spacing: 0.9px;
                border-bottom: 5px solid #5e42b0;
                margin-right: 12px;
            }
            &__txt {
                color: #c1c1c1;
                font-size: 36px;
                letter-spacing: 1.8px;
            }
        }
        &__text {
            margin-top: 30px;
            color: #000000;
            font-family: 'Proxima_Nova_Rg';
            font-size: 20px;
            letter-spacing: 1px;
        }
    }
    &__2 {
        position: relative;
        background-color: #5e42b0;
        &::before {
            content: "";
            display: block;
            position: absolute;
            background-image: url(../img/footboll.png);
            background-size: 100%;
            background-repeat: no-repeat;
            width: 36.5%;
            height: 950px;
            bottom: -30px;
            right: 50%;
        }
        &__cart {
            margin-top: 0;
            &__nomber {
                border-color:  #efefef;
            }
            &__text {
                color: #ffffff;
            }
        }
    }
    &__3 {
        position: relative;
        background-color: #0a0e34;
        &::after {
            content: "";
            display: block;
            position: absolute;
            background-image: url(../img/basket.png);
            background-size: 100%;
            background-repeat: no-repeat;
            width: 52%;
            height: 815px;
            bottom: 160px;
            left: 45%;
        }
        &__cart__nomber {
            color: #9173e6;            
            border-color: #efefef;
        }
    }
}
.part3 {
    &__block {
        position: relative;
        display: flex;
        justify-content: space-between;
        &:first-child::before {
            content: "";
            display: block;
            width: 50%;
            background-image: url(../img/block1.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
        &:nth-child(2)::after {
            content: "";
            display: block;
            width: 50%;
            background-image: url(../img/block2.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
        &:last-child::before {
            content: "";
            display: block;
            width: 50%;
            background-image: url(../img/block3.png);
            background-repeat: no-repeat;
            background-size: cover;
        }
        &__rectangle {
            top: 211px;
            left: 50%;
            margin-left: -84px;
            position: absolute;
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            width: 168px;
            height: 155px;
            background-color: #5e42b0;
            &__nomber {
                color: #f5f6f7;
                font-family: 'Proxima_Nova_Bold';
                font-size: 24px;
                line-height: 34px;
                text-transform: uppercase;
                letter-spacing: 1.2px;
                &::after {
                    content: "";                    
                    display: block;
                    width: 22px;
                    height: 5px;
                    border-radius: 2px;
                    background-color: #ffffff;
                    opacity: 0.3;
                }
            }
        }
        &__card {
            width: 50%;
            box-sizing: border-box;
            margin-top: 257px;
            padding-left: 127px;
            margin-bottom: 122px;
            &__head {
                opacity: 0.4;
                color: #c1c1c1;
                font-family: 'Proxima_Nova_Bold';
                font-size: 90px;
                line-height: 60px;
                text-transform: uppercase;
            }
            &__text {
                margin-top: 115px;
                width: 53.22%;
                color: #000000;
                font-family: 'Proxima_Nova_Rg';
                font-size: 24px;
                line-height: 28px;
                letter-spacing: 1.2px;
            }
            &::after {
                content: "";
                margin-top: 87px;
                display: block;
                width: 22px;
                height: 5px;
                border-radius: 2px;
                background-color: #5e42b0;
            }
        }
    }
}
.part4 {
    background-image: url(../img/bg_bottom.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-color: #202448;
    &__wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    &__head {
        margin: 0 auto;
        padding-top: 229px;
        display: flex;
        align-items: center;
        &::before {
            content: "";
            display: block;
            width: 130px;
            height: 137px;
            background-image: url(../img/down_logo.png);
        }
        &__text {
            margin-left: 36px;
            text-transform: uppercase;
            font-family: 'Proxima_Nova_Bold';
            &__h1 {
                color: #ffffff;
                font-size: 90px;
                line-height: 60px;
                letter-spacing: 4.96px;
            }
            &__h2 {
                margin-top: 14px;
                color: #a496ca;
                font-size: 36px;
                line-height: 34px;
            }
        }
    }
    .down_button {
        width: 33.13%;
        &__block {
            padding-top: 163px;
            padding-bottom: 197px;
        }
    }
    &__bottom {        
        margin: 71px 50px 0 50px;
        padding-bottom: 67px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &::before {
            content: "";
            display: inline-block;
            width: 189px;
            height: 33px;
            background-image: url(../img/head_logo.png);
            background-repeat: no-repeat;
        }
        &__social {
            display: flex;
            align-items: center;
            margin-right: -10%;
            &__link {
                margin-right: 40px;
                display: block;
                background-image: url(../img/twitt.png);
                width: 27px;
                height: 22px;                
            
                &:first-child {
                    background-image: url(../img/face.png);
                    width: 15px;
                    height: 27px; 
                }
                &:last-child {
                    background-image: url(../img/inst.png);
                    width: 24px;
                    height: 25px;
                }
            }
        }
        &__text {
            margin-right: 10px;
            color: #ffffff;
            font-family: 'Proxima_Nova_Rg';
            font-size: 20px;
            letter-spacing: 1px;
        }
    }
}